/**
 * Diese Klasse beinhaltet nur die ID des AI Models, die auf das Model in der Datenbank verweist.
 */
export class AIModel {
    private id: number; //Die AI Model ID.

    /**
     * Die bergebene ID muss mit der AIModel ID in der Datenbank bereinstimmen.
     * @param id AIModel ID
     */
    constructor(id: number) {
        this.id = id;
    }

    /**
     * Gibt die AIModel ID zurck.
     */
    getID(): number {
        return this.id;
    }
}