import React, { Component } from 'react';
import { NotificationManager } from 'react-notifications';

/**
 * Modelliste von gewhltes Projekt
 */
export default class ModelList extends Component {

    /**
     * Variablen und Methoden welche der Klasse zur verfgung gestellt werden mssen
     */
    props = {
        currentProject: { projectID: -1, projectName: "null", AIModelID: [-1] },
        pageLoadModel: function (chosenmodelID: number) { }
    };

    /**
     * Status fr diese Komponente
     */
    state = {
        chosenmodelID: -1
    };

    /**
     * Selektieren in Modelliste
     * @param e ChangeEvent
     */
    handleChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
        this.setState({
            chosenmodelID: e.target.value
        });
    };

    /**
     * Methode fr Besttigung Knopf
     */
    handleChoose() {
        /* wait to change load model*/
        if (this.state.chosenmodelID === -1) {
            NotificationManager.error('Sie haben noch kein Modell gewhlt!', "", 3000);
        } else {
            this.props.pageLoadModel(this.state.chosenmodelID);
        }
    }

    /**
     * Render Methode des Komponenten
     * @returns Aufbau des Komponenten
     */
    render() {
        return (
            <section>
                <label>Modell-Liste</label>
                <select onChange={this.handleChange}>
                    <option>Modell auswhlen</option>
                    {this.props.currentProject.AIModelID.map((modelObj) => {
                        return <option value={modelObj} key={modelObj} >Modell{modelObj}</option>;
                    })}
                </select>
                <button onClick={() => this.handleChoose()} className="btn" >Gewhltes Modell laden (zur Auslieferungsseite)</button>
            </section>
        );
    }
}
