import { Page } from "../PageInterface";
import { PageController } from "../../../controller/PageController";
import { State } from "./State";
import ReactDOM from 'react-dom';
import { NotificationContainer } from 'react-notifications';
import 'react-notifications/lib/notifications.css';
import './Result.css';


/**
 * Die Darstellungsseite fr die Startseite der Datenerfasser
 */
export class ResultPage implements Page {
    private state: State;
    private observers: PageController[] = [];

    private static readonly PAGE_TITLE = "Ergebnis"
    private static readonly CLASSIFY = "Klassifiziere, bitte warten"
    private static readonly RELOAD = "Erneut klassifizieren"

    /**
     * Konstruktor der Darstellungseite
     * @param admin Name der Person fr den aufgenommen wird
     */
    constructor() {
        this.state = new State();
        this.update();
    }

    /**
    * Update Methode der Darstellungsseite. Diese Methode wird nach jeder nderung, die kein Seitenwechsel ist, aufgerufen. 
    * Die Methode enthlt den Aufbau der Seite und wird von ihr gerendert.
    * Es werden durch notify() alle controller ber ein Update informiert und alle Seiten Elemente werden aktualisiert und erneut gerendert. 
    */
    private update() {
        this.notify();
        let VDOM = (
            <div>
                <h2 className='title'>{ResultPage.PAGE_TITLE}</h2>
                <h1 className='result'>{ResultPage.CLASSIFY}</h1>
                <NotificationContainer />
            </div>)
        if (this.state.aiUserData!.result !== " ") {
            VDOM = (
                <div>
                    <h2 className='title'>Ergebnis</h2>
                    <h1 className="result">{this.state.aiUserData!.result}</h1>
                    <button className='button' onClick = {this.reloadPage}>{ResultPage.RELOAD}</button>
                    <NotificationContainer />
                </div>
            );
        }
        if (document.getElementById('root') !== null) {
            ReactDOM.render(VDOM, document.getElementById('root'));
        }
    }


    reloadPage() {
        window.location.reload()
    }

    /**
    * Durch diese Methode kann sich ein Controller als Beobachter anmelden.
    * @param oberver neuer Beobachter
    */
    attach(observer: PageController) {
        this.observers.push(observer);
    }

    /**
    * Durch diese Methode kann sich ein Controller als Beobachter abmelden.
    * @param oberver Beobachter der zu entfernen ist
    */
    detach(observer: PageController) {
        const index = this.observers.indexOf(observer, 0);
        if (index > -1) {
            this.observers.splice(index, 1);
        }
    }

    /**
    * Durch diese Methode werden alle Beobachter ber eine nderung auf der Seite informiert.
    */
    notify() {
        for (let index = 0; index < this.observers.length; index++) {
            const element = this.observers[index];
            element.update();
        }
    }

    /**
    * Gibt den Status der Seite zurck
    */
    getState() {
        return this.state;
    }

    /**
     * Setzt einen neuen Zustand fr die Seite und aktualisiert sie
     * @param state neuer Zustand fr die Seite
     */
    setState(state: any) {
        this.state = state;
        this.update();
    }
}