/**
 * Diese Klasse verwaltet die Interaktion mit dem Python-Backend und die zugehrige Kommunikation.
 * Sie ist verantwortlich fr den Bau von KI-Modellen und fr die Ausfhrung derselben.
 */
export class AIBuilder {
    private modelId: number;
    private static readonly url: string = "../";
    private static readonly buildModel: string = "src/buildModel/";
    private static readonly classify: string = "src/classify/";

    /**
     * Erstellt den AIBuilder und bergibt ihm die ID des zu bearbeitenden Modells.
     * @param modelId Die ID des zu erstellenden oder zu verwendenden Modells
     */
    constructor(modelId: number) {
        this.modelId = modelId;
    }

    /**
     * Ruft die Python-Schnittstelle auf dem Server auf, um ein KI-Modell zu erstellen und zu trainieren.
     * @param modelData Smtliche relevanten Daten, um das gewnschte KI-Modell erstellen zu knnen.
     * Diese mssen das folgende Format haben, wenn zu JSON gepackt:
     * ```
     * {
     *     "dataSets": [
     *         42
     *     ],
     *     "classifier": "Classifier",
     *     "scaler": "Scaler",
     *     "features": [
     *         "featurename#13"
     *     ],
     *     "projectID": 1,
     *     "trainingDataPercentage": 0.8, // optional
     *     "slidingWindowSize": 128,      // optional
     *     "slidingWindowStep": 64        // optional
     * }
     * ```
     * Die Reihenfolge ist unwichtig.
     */
    applyModel(dataSets: number[], imputator: string, classifier: string, scaler: string, features: string[], projectID: number, trainingDataPercentage: number = 1, slidingWindowSize: number = 128, slidingWindowStep: number = 64): void {
        const modelData = {
            "dataSets":               dataSets,
            "imputator":              imputator,
            "classifier":             classifier,
            "scaler":                 scaler,
            "features":               features,
            "projectID":              projectID,
            "trainingDataPercentage": trainingDataPercentage,
            "slidingWindowSize":      slidingWindowSize,
            "slidingWindowStep":      slidingWindowStep
        }
        this.sendRequest(JSON.stringify(modelData), AIBuilder.url + AIBuilder.buildModel, console.log);
    }

    /**
     * Diese Methode ruft die Python-Schnittstelle auf dem Server auf, um ein KI-Modell einen Datensatz klassifizieren zu lassen.
     * @param dataSetId Die ID des Datensatzes, den das KI-Modell klassifizieren soll.
     * @param callBack Eine Rckmelde-Funktion des "aufrufenden" Controllers, mit der das Ergebnis der Klassifizierung an den Controller zurckgegeben wird.
     */
    classify(dataSetId: number, callBack: <R = unknown>(prediction: string | object) => R): void {
        let requestData: string = JSON.stringify({ "classifier": this.modelId, "dataSet": dataSetId });
        this.sendRequest(requestData, AIBuilder.url + AIBuilder.classify, callBack);
    }

    /**
     * Diese private Methode fsst den Versandprozess der Subanfrage an den Anwendungsserver zusammen.
     * @param requestData Die mit der Anfrage zu versendenden Daten. Hat gltiges JSON zu sein, auer plainText ist gesetzt.
     * @param url Die Adresse, an die die Anfrage gestellt wird. Am besten was auf demselben Server, ansonsten bekommt der Browser nen Kasper.
     * @param successAction Eine Funktion, die bei erfolgreicher Durchfhrung der Anfrage auszufhren ist.
     * @param plainText Gibt an, dass die Daten als Plaintext zu versenden sind.
     */
    private sendRequest(requestData: string, url: string, successAction: <R = unknown>(param: string | object) => R, plainText: boolean = false) {
        let xhr = new XMLHttpRequest(); // XHR ist kurz fr XmlHttpRequest
        xhr.open("POST", url, true);
        xhr.onreadystatechange = () => {
            if (xhr.readyState === 4) {
                if (xhr.status === 200) {
                    var responseJSON: object | null;
                    try {
                        responseJSON = JSON.parse(xhr.responseText);
                    }
                    catch (e) {
                        responseJSON = null;
                    }
                    if (responseJSON == null) {
                        successAction(xhr.responseText);
                    }
                    else {
                        successAction(responseJSON);
                    }
                }
                else if (xhr.status === 406) {
                    throw new Error("Error in request data - not accepted by Server");
                }
                else {
                    xhr.send(requestData);
                }
            }
        };
        if (!plainText) {
            xhr.setRequestHeader("Content-Type", "application/json");
        }
        xhr.send(requestData);
    }
}
